.item {
  width: 190px;
  height: 280px;
  border-radius: 4px;
  box-shadow: 3px 7px 5px -3px rgba(0, 0, 0, 0.39);
  overflow: hidden;
  background-color: #f5f5f5;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 12px;
}

.img {
  display: block;
  max-width: 100%;
  height: 80%;
  object-fit: cover;
}

.info {
  padding: 10px 10px;
}
